import SectionFacility from '../components/sections/facility';
import SectionPricing from '../components/sections/pricing';
import SectionBooking from '../components/sections/booking';
import SectionAccess from '../components/sections/access';
import SectionContact from '../components/sections/contact';

const loadScript = scriptObject => {
  return new Promise((resolve, reject) => {
    const script = document.createElement('script');
    script.onload = () => resolve(true);
    script.onerror = () => reject(false);
    script.src = scriptObject.src;
    script.integrity = scriptObject.integrity;
    script.crossOrigin = scriptObject.crossOrigin;

    document.body.appendChild(script);
  });
};

// @TODO from gatsby-config
const title = 'addict gym';
const url = 'https://addict-gym.com/';
const description =
  '東京・千駄ヶ谷にあるレンタルルームジム『addict gym』。贅沢な空間で上質なトレーニングを何度でも体験できます。';

const theme = createTheme({
  default: '#ffffff',
  color: '#ffffff',
  root: {
    dark: '#ffffff',
    color: '#ffffff'
  },
  palette: {
    default: {
      color: '#ffffff'
    },
    primary: {
      main: '#b28223'
    },
    text: {
      primary: '#00ff00',
      dark: '#ff00ff',
      secondary: '#ff0000'
    }
  },
  typography: {
    fontSize: 12,
    fontFamily: ['YuMincho', 'serif'].join(','),
    h4: {
      fontSize: 32
    },
    h5: {
      fontSize: 28
    }
  }
});

const IndexPage = () => {
  useEffect(() => {
    (async () => {
      await loadScript({
        src: 'https://code.jquery.com/jquery-3.6.0.min.js',
        integrity: 'sha256-/xUj+3OJU5yExlq6GSYGSHk7tPXikynS7ogEvDej/m4=',
        crossOrigin: 'anonymous'
      });
    })();
  });

  return (
    <ThemeProvider theme={theme}>
      <Helmet>
        <title>{title}</title>
        <meta name='description' content={description} />
        <meta property='og:title' content={title} />
        <meta property='og:type' content='website' />
        <meta property='og:url' content={url} />
        <meta property='og:description' content={description} />
        <meta property='og:image' content={`${url}ogp.png`} />
        <meta property='og:image:secure_url' content={`${url}ogp.png`} />
        <meta property='og:image:type' content='image/png' />
        <meta property='og:image:width' content={1200} />
        <meta property='og:image:height' content={630} />
        <meta property='og:locale' content='ja_JP' />
        <style>{`
          html, body {
            margin: 0;
            padding: 0;
          }
          a:hover {
            color: #fff;
          }
          `}</style>
      </Helmet>

      <SectionMenu />
      <SectionMainvisual />
      <SectionAbout />

      <Container maxWidth={false}>
        <Typography
          sx={{
            display: {
              xs: 'none',
              md: 'block'
            },
            marginTop: '-10vw',
            marginBottom: '-6vw',
            fontSize: '18vw',
            fontWeight: 800,
            textAlign: 'center',
            color: '#f7f3e9',
            pointerEvents: 'none'
          }}
        >
          addict gym
        </Typography>
      </Container>

      <SectionFacility />
      <SectionPricing />
      <SectionBooking />
      <SectionAccess />
      <SectionContact />
    </ThemeProvider>
  );
};

export default IndexPage;
